@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #121212;
  font-size: 16px;
  line-height: 24px;
}

img {
  max-width: 100%;
}

.t1 {
  font-family: "Roboto", sans-serif;
  font-size: 42px;
  font-weight: 700;
  line-height: 50px;
}

.btn,
.btn:visited {
  display: inline-block;
  background: #E4C1AE;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  padding: 16px 40px;
  border-radius: 32px;
  transition: 0.3s all;
  text-decoration: none;
}
.btn:hover, .btn:focus, .btn.btn--active,
.btn:visited:hover,
.btn:visited:focus,
.btn:visited.btn--active {
  background: #d7a488;
}

.offer-img {
  padding: 30px 0;
}
.offer-img__container {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  padding-left: 230px;
  padding-right: 230px;
}
.offer-img__img, .offer-img__content {
  flex-basis: 100%;
}
.offer-img__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.offer-img__title {
  padding-bottom: 24px;
}
.offer-img__description {
  padding-bottom: 32px;
}
.offer-img__title, .offer-img__description, .offer-img__url {
  flex-basis: 100%;
}
@media (min-width: 768px) {
  .offer-img {
    padding: 96px 0;
  }
  .offer-img__container {
    flex-wrap: wrap;
  }
  .offer-img__content {
    flex-basis: 41.6666666667%;
  }
  .offer-img__img {
    flex-basis: 58.3333333333%;
    text-align: center;
  }
  .offer-img__img img {
    max-width: 505px;
  }
}